// src/ThemeContext.tsx
import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';

// Define los tipos de temas disponibles. Puedes expandirlo si tienes ms temas.
type Theme = 'light' | 'dark';

// Define la estructura del contexto del tema
interface ThemeContextType {
  theme: Theme;
  toggleTheme: () => void;
}

// Crea el contexto con un valor inicial indefinido
const ThemeContext = createContext<ThemeContextType | undefined>(undefined);

// Proveedor del contexto del tema
export const ThemeProvider: React.FC<{ children: ReactNode }> = ({ children }) => {
  // Estado para el tema actual. Intenta leerlo de localStorage si ya existe.
  const [theme, setTheme] = useState<Theme>(() => {
    const storedTheme = localStorage.getItem('theme');
    return (storedTheme === 'light' || storedTheme === 'dark') ? storedTheme : 'light'; // Tema por defecto 'light'
  });

  // Efecto para aplicar la clase CSS al body y guardar el tema en localStorage
  useEffect(() => {
    document.body.setAttribute('data-theme', theme); // Aade un atributo data-theme al body
    localStorage.setItem('theme', theme); // Guarda la preferencia del tema
  }, [theme]);

  // Funcin para alternar entre temas
  const toggleTheme = () => {
    setTheme(prevTheme => (prevTheme === 'light' ? 'dark' : 'light'));
  };

  // Valor del contexto
  const contextValue: ThemeContextType = {
    theme,
    toggleTheme,
  };

  return (
    <ThemeContext.Provider value={contextValue}>
      {children}
    </ThemeContext.Provider>
  );
};

// Hook personalizado para usar el contexto del tema
export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (context === undefined) {
    throw new Error('useTheme debe usarse dentro de un ThemeProvider');
  }
  return context;
};